import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useHistory } from 'react-router-dom';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import TrendingUpIcon from '@material-ui/icons/TrendingUp';
import WhatshotIcon from '@material-ui/icons/Whatshot';
import LiveTvIcon from '@material-ui/icons/LiveTv';
import SearchIcon from '@material-ui/icons/Search';

const useStyles = makeStyles({
  root: {
    position: 'fixed',
    bottom: 0,
    width: 100 + '%',
    backgroundColor: '#1d1d27',
    zIndex: 100
  },
});

export default function SimpleBottomNavigation() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  const history = useHistory();

  useEffect(() => {
    if ( value === 0 ) {
      history.push('/');
    } else if ( value === 1 ) {
      history.push('/popular');
    } else if ( value === 2 ) {
      history.push('/series');
    } else if ( value === 3 ) {
      history.push('/search');
    }
  }, [value, history]);

  return (
    <BottomNavigation
      value={value}
      onChange={(event, newValue) => {
        setValue(newValue);
      }}
      showLabels
      className={classes.root}
    >
      <BottomNavigationAction style={{ color: '#909090' }} label="RECENTS" icon={<TrendingUpIcon />} />
      <BottomNavigationAction style={{ color: '#909090' }} label="POPULAR" icon={<WhatshotIcon />} />
      <BottomNavigationAction style={{ color: '#909090' }} label="TV SERIES" icon={<LiveTvIcon />} />
      <BottomNavigationAction style={{ color: '#909090' }} label="SEARCH" icon={<SearchIcon />} />
    </BottomNavigation>
  );
}
