import React from 'react';
import Badge from '@material-ui/core/Badge';
import { img_300 } from '../config/config';
import './SingleContent.css'

function SingleContent({ 
  id,
  poster,
  title,
  date,
  media_type,
  vote_average,
 }) {
  return (
    <article className="movie-box">
      <Badge 
        badgeContent={vote_average}
        color={vote_average > 6? 'primary' : 'secondary'}
      />
      <img className="movie-box__poster" src={`${img_300}/${poster}`} alt={title} />
      <h4 className="movie-box__title">{title}</h4>
      <div className="movie-box__des">
        <span className="movie-box__type">{media_type === "tv" ? "TV Series" : "Movie"}</span>
        <span className="movie-box__date">{date}</span>
      </div>
    </article>
  );
}

export default SingleContent;